<template>
  <div id="app" class="flex-row" style="overflow:auto">
    <div class="pr area-wrap oh">
        <!-- 底层 -->
        <div class="layer">
          <div class="typeMarks">
            <div v-for="(type,index) in buildingTypes" :key="'color'+index" class="_mark" :style="{color:type.fill}">{{type.title}}</div>
          </div>
        </div>
        <!-- svg地图层 -->
        <div class="layer" id="svgElements">
          <svg width="1000" height="1000" xmlns="http://www.w3.org/2000/svg" @click.self="resetSvg">
          <template v-for="(item,index) in polygons">
            <polygon :key="'polygon'+index"
            :class="{isBlock: item.block, svgArea: true}"
            :style="item.type"
            :points="item.points" :data-x="item.attrDataX" :data-y="item.attrDataY" :title="item.attrTitle"
            @click="clickElement($event, item)" />
          </template>
          </svg>
        </div>
        <!-- 漂浮层 -->
        <div class="layer mapFloat">
          <template v-if="!item.block" v-for="(item,index) in polygons">
            <span :key="'float'+index" class="_title" :style="{left:item.attrDataX+'px', top:item.attrDataY+'px'}">{{item.attrTitle}}</span>
          </template>
        </div>
        <!-- 路径绘制层 -->
        <div class="layer svgPath" v-if="mode==='路径绘制' || cachePointMode">
          <div class="layer drawPath" @click="drawPath">
            <!-- 当前绘制的路径 -->
            <div class="drawPoint" v-for="(item,index) in currentPath" :key="'drawpoint'+index"
              v-if="!cachePointMode"
              :title="item.x+','+item.y"
              :class="{active: item.active}"
              :style="{left: item.x+'px', top: item.y+'px'}"
              @click.stop="clickPoint(item)"
              @mousedown="startPoint($event, item)"></div>
            <!-- 已保存的路径 -->
            <div v-for="(item,index) in roads" :key="'showPath'+index">
              <div class="drawPoint" v-for="(item,drawPoint) in item" :key="'showPathPoint'+drawPoint"
                :title="'路径'+(index+1)+' '+item.x+','+item.y"
                :style="{left: item.x+'px', top: item.y+'px'}"
                @click.stop="clickPoint(item)"
                @mousedown="startPoint($event, item)"></div>
            </div>
          </div>
          <svg width="1000" height="1000" xmlns="http://www.w3.org/2000/svg">
            <!-- 当前绘制的路径 -->
            <polyline :points="currentPath.map(e=>{return e.x+','+e.y}).join(' ')" 
              v-if="!cachePointMode"
              class="editingPath" />
            <!-- 已保存的路径 -->
            <polyline v-for="(item,index) in roads" :key="'showPath'+index" :points="item.map(e=>{return e.x+','+e.y}).join(' ')" 
              :class="{editingPath: true,active:index===currentEditingPathIndex}"/>
          </svg>
        </div>
    </div>
    <div class="flex-1" style="margin:20px">
      <div class="flex-row">
        <div class="flex-1">
          <el-upload class="myUpload"
            drag
            action=""
            accept="image/svg+xml"
            :before-upload="getFile">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">导入地图svg文件</div>
          </el-upload>
        </div>
        <div class="flex-1">
          <el-upload class="myUpload"
            drag
            action=""
            accept="application/json"
            :before-upload="importMap">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">导入地图json文件</div>
          </el-upload>
        </div>
        <div class="flex-1">
          <el-upload class="myUpload"
            drag
            action=""
            accept="application/json"
            :before-upload="importPath">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">导入路径json文件</div>
          </el-upload>
        </div>
      </div>
      <div class="flex-row">
        <el-radio-group v-model="mode" size="small">
          <el-radio-button label="编辑地图"></el-radio-button>
          <el-radio-button label="路径绘制"></el-radio-button>
        </el-radio-group>
        <div class="flex-1" style="text-align:right">
          <a id="mapDataDownload" download="map.json" target="_blank"><el-button icon="el-icon-download" size="small">保存地图数据</el-button></a>
          <a id="pathDataDownload" download="path.json" target="_blank"><el-button icon="el-icon-download" size="small" :disabled="!roads.length">保存路线数据</el-button></a>
          <el-button type="danger" icon="el-icon-warning" size="small" :disabled="!polygonsBackup" @click="resetData">重做</el-button>
        </div>
      </div>
      <!-- 地图编辑面板 -->
      <el-form label-width="100px" style="margin:40px 0 0" v-show="mode==='编辑地图'">
        <template v-if="currentPolygon.points">
          <el-form-item label="标题">
            <el-input v-model="currentPolygon.attrTitle" id="infoInputer"></el-input>
          </el-form-item>
          <el-form-item label="描述">
            <el-input v-model="currentPolygon.desc" type="textarea"></el-input>
          </el-form-item>
          <el-form-item label="所属类型">
            <el-select v-model="currentPolygon.type" value-key="title" placeholder="请选择">
              <el-option
                v-for="item in buildingTypes"
                :key="item.title"
                :label="item.title"
                :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          
          <el-form-item label="关联路径节点">
            <el-button :disabled="currentPolygon.block" icon="el-icon-location" circle @click="cachePointMode=true"></el-button>
            <el-button v-show="cachePointMode" @click="cachePointMode=false">退出拾取</el-button>
            <el-button v-show="currentPolygon.connectPoint && currentPolygon.connectPoint.x" @click="breakConnect()">清除关联</el-button>
            <div v-if="currentPolygon.connectPoint && currentPolygon.connectPoint.x">
              {{JSON.stringify(currentPolygon.connectPoint)}}
            </div>
          </el-form-item>
          <el-form-item label="忽略建筑物">
            <el-switch v-model="currentPolygon.block"></el-switch>
          </el-form-item>
        </template>
        <template v-else>
          <div>从左侧地图中选择区域，单击进入编辑</div>
        </template>
      </el-form>
      <!-- 路线绘制面板 -->
      <div v-show="mode==='路径绘制' && !cachePointMode">
        <!-- 已保存路径 -->
        <div class="m pathList">
          <el-tag v-for="(item,index) in roads" :key="'road'+index"
          class="_pathtag"
          :type="index === currentEditingPathIndex ? 'success' : 'info'"
          @click.native="choosePath(index)">路径{{index+1}}</el-tag>
        </div>
        <!-- 当前路径 -->
        <el-table ref="currentRoadsPointsTable" style="width: 100%" empty-text="在左侧地图上开始绘制"
          :data="currentPath" 
          :highlight-current-row="true"
          @current-change="triggerCurrentFromTable">
          <el-table-column type="index" width="50"></el-table-column>
          <el-table-column label="坐标" width="150">
          <template slot-scope="scope">
            X: {{scope.row.x}} Y: {{scope.row.y}}
          </template>
          </el-table-column>
          <el-table-column label="关联建筑">
          <template slot-scope="scope">
            <div v-if="scope.row.connectBuildings">
              {{scope.row.connectBuildings.join('、')}}
            </div>
          </template>
          </el-table-column>
          <el-table-column label="操作" width="280" align="center">
            <template slot-scope="scope">
              <el-button
              size="mini"
              type="primary"
              @click="insertPoint(scope.$index, -1)">前添加</el-button>
              <el-button
              size="mini"
              type="primary"
              @click="insertPoint(scope.$index, 1)">后添加</el-button>
              <el-button
              size="mini"
              type="danger"
              @click="deleteCurrentPathPoint(scope.$index)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="tc m">
          <el-button type="primary" size="small" :disabled="!currentPath || !currentPath.length" @click="addPath">保存路径</el-button>
          <el-button size="small" :disabled="!currentPath || !currentPath.length" @click="clearPath">清除节点</el-button>
          <el-button type="danger" size="small" :disabled="!currentEditingPathIndex" @click="deletePath(currentEditingPathIndex)">删除路径</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as util from "./assets/util";
import * as config from "./assets/config";

let pointMovingCache = {};
let mouseMoveHandle = function(evt){
  let point = pointMovingCache.point
  if(point){
    
    let xMove = evt.clientX - pointMovingCache.x;
    let yMove = evt.clientY - pointMovingCache.y;
    if(xMove!==0 || yMove!==0){
      point.isMoving = true;
      point.x += xMove;
      point.y += yMove;
      pointMovingCache.x = evt.clientX;
      pointMovingCache.y = evt.clientY;
    }
  }
};
let mosueUpHandle = function(){
  
  document.removeEventListener('mousemove', mouseMoveHandle);
  document.removeEventListener('mouseup', mosueUpHandle);
}

export default {
  name: "app",
  data() {
    return {
      mainDom: null,
      mapDataDownloadDom: null,
      pathDataDownloadDom: null,
      polygonsBackup: null,
      polygons: [],
      roads: [],
      currentPolygon: {},
      mode: '编辑地图',
      cachePointMode: false,
      currentPath: [],
      currentEditingPathIndex: null,
      buildingTypes: config.buildingType
    };
  },
  watch: {
    mode: function(){
      this.resetSvg()
    }, 
    polygons: {
      handler: function(polygons){
        util.session('editing-polygons', polygons);
        // 保存地图数据
        if(this.mapDataDownloadDom){
          let mapStr = this.polygons.map((e, i)=>{
            if(i===0){
              return '['+ JSON.stringify(e)+ ','
            }else if(i===this.polygons.length-1){
              return JSON.stringify(e) + ']'
            }else{
              return JSON.stringify(e) + ','
            }
          });
          var myBlob = new Blob(mapStr, { "type" : "application/json" });
          this.mapDataDownloadDom.href = window.URL.createObjectURL(myBlob);
        }
      },
      deep: true
    }
  },
  methods: {
    triggerCurrentFromTable: function(currentRow, prevCurrent){
      if(currentRow){
        currentRow.active = true;
      }
      if(prevCurrent){
        delete prevCurrent.active;
      }
      this.$nextTick(() => {
        this.$forceUpdate()
      })
    },
    insertPoint: function(index, place){
      let thePoint = this.currentPath[index];
      let targetPoint;
      let newPoint;
      let insertIndex;
      if(place<0){
        if(index>0){
          targetPoint = this.currentPath[index-1];
        }else{
          let nextPoint = this.currentPath[index+1];
          let distance = {
            x: thePoint.x - nextPoint.x,
            y: thePoint.y - nextPoint.y
          }
          targetPoint = {
            x: thePoint.x + distance.x,
            y: thePoint.y + distance.y
          }
        }

        insertIndex = index;
      }else{
        if(index===this.currentPath.length-1){
          let prevPoint = this.currentPath[index-1];
          let distance = {
            x: thePoint.x - prevPoint.x,
            y: thePoint.y - prevPoint.y
          }
          targetPoint = {
            x: thePoint.x + distance.x,
            y: thePoint.y + distance.y
          }
        }else{
          targetPoint = this.currentPath[index+1]
        }

        insertIndex = index+1;
      }

      newPoint = {
        x: parseInt((thePoint.x + targetPoint.x)/2),
        y: parseInt((thePoint.y + targetPoint.y)/2),
      }
      
      this.currentPath.splice(insertIndex, 0, newPoint);

    },
    startPoint: function(evt, point){
      pointMovingCache = {
        x: evt.clientX,
        y: evt.clientY,
        point,
        pointCache: util.deepcopy(point)
      }
      document.addEventListener('mousemove', mouseMoveHandle);
      document.addEventListener('mouseup', mosueUpHandle);
    },
    clickPoint: function(point){
      if(point.isMoving){
        // 拖动点
        let editingPoint = pointMovingCache.point
        let pointCache = pointMovingCache.pointCache
        if(pointCache && pointCache.connect){
          //同步数据
          this.roads.forEach(road => {
            road.forEach(sp => {
              if(sp.connect && sp.x=== pointCache.x && sp.y=== pointCache.y){
                sp.x = editingPoint.x;
                sp.y = editingPoint.y;
              }
            })
          })
        }
        pointMovingCache = {};
        delete point.isMoving;
      }else if(this.cachePointMode){
        //拾取模式
        this.currentPolygon.connectPoint = point;
        if(!point.connectBuildings){
          point.connectBuildings = []
        }
        let isIn = point.connectBuildings.indexOf(this.currentPolygon.attrTitle);
        if(isIn<0){
          point.connectBuildings.push(this.currentPolygon.attrTitle)
        }
        
        this.cachePointMode = false;
        this.savePath();
      }else{
        let currentPathIndex = util.pointInArray(point, this.currentPath);
        if(point.active){
          //取消高亮
          this.$refs.currentRoadsPointsTable.setCurrentRow()
        }else if(currentPathIndex!==null){
          //当前路径节点高亮
          this.$refs.currentRoadsPointsTable.setCurrentRow(point)
        }else{
          //链接到已保存路径
          this.currentPath.push(point);
          point.connect = true;
        }
        
      }
    },
    breakConnect: function(){
      let isIn = this.currentPolygon.connectPoint.connectBuildings.indexOf(this.currentPolygon.attrTitle);
      if(isIn>=0){
        this.currentPolygon.connectPoint.connectBuildings.splice(isIn,1);
      }
      delete this.currentPolygon.connectPoint;
    },
    drawPath: function(event){
      if(pointMovingCache && pointMovingCache.point){
        pointMovingCache = {}
      }else{
        this.currentPath.push({
          x: event.offsetX,
          y: event.offsetY
        })
      }
    },
    deleteCurrentPathPoint: function(index){
      this.currentPath.splice(index, 1);
    },
    deletePath: function(index){
      this.$confirm('删除路径'+(index+1)+'?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.roads.splice(index, 1);
        this.currentPath = [];
        this.savePath();
      })
    },
    choosePath: function(index){
      if(this.currentEditingPathIndex === index){
        this.currentPath = [];
        this.currentEditingPathIndex = null;
        return null;
      }
      this.currentEditingPathIndex = index;
      this.currentPath = this.roads[index]
    },
    clearPath: function(){
      this.currentPath = [];
      if(this.currentEditingPathIndex !== null){
        this.deletePath(this.currentEditingPathIndex)
      }
    },
    addPath: function(){
      if(this.currentEditingPathIndex){
        this.roads.splice(this.currentEditingPathIndex,1,util.deepcopy(this.currentPath))
      }else{
        this.roads.push(util.deepcopy(this.currentPath));
      }
      this.currentPath = [];
      this.currentEditingPathIndex = null;
      this.savePath();
    },
    savePath: function(){
      util.session('editing-roads', this.roads);
      // 保存路径数据
      if(this.pathDataDownloadDom){
        let blobStr = this.roads.map((e,i) => {
          if(i===0){
            return '['+ JSON.stringify(e)+ ','
          }else if(i===this.roads.length-1){
            return JSON.stringify(e) + ']'
          }else{
            return JSON.stringify(e) + ','
          }
        });
        var myBlob = new Blob(blobStr, { "type" : "application/json" });
        this.pathDataDownloadDom.href = window.URL.createObjectURL(myBlob);
      }
    },
    resetData: function(){
      this.resetSvg()
      if(Array.isArray(this.polygonsBackup)){
        this.polygons = util.deepcopy(this.polygonsBackup);
      }
    },
    resetSvg: function(){
      this.currentPolygon = {}
      if(this.mainDom){
        let actived = this.mainDom.querySelectorAll('polygon.active');
        actived.forEach(polygon => {
          polygon.classList.remove('active');
        })
      }
    },
    clickElement: function(evt, item){
      this.resetSvg();
      evt.target.classList.add('active');
      this.currentPolygon = item;
      this.$nextTick(() => {
        document.getElementById('infoInputer').focus()
      })
    }, 
    reset: function(){
      this.currentPolygon = {};
      this.currentPath = [];
      this.mode="编辑地图";
    },
    getFile: function(file) {
      let vm = this;
      vm.reset();
      util.readLocalFile(file, result => {
        util.loadSvg(result, function(polygonsArray){
          vm.polygonsBackup = polygonsArray;
          vm.polygons = util.deepcopy(polygonsArray);
        });
      });
      return false;
    },
    importMap: function(file){
      let vm = this;
      vm.reset();
      util.readLocalFile(file, result => {
        let mapData = JSON.parse(result)
        mapData.forEach(m => {
          if(!m.connectPoint){
            m.connectPoint = {}
          }
        })
        this.polygons = mapData
        util.session('editing-polygons', mapData);
      });
      return false;
    },
    importPath: function(file){
      let vm = this;
      vm.reset();
      util.readLocalFile(file, result => {
        this.roads = JSON.parse(result)
        util.session('editing-roads',this.roads);
      });
      return false;
    }
  },
  created: function() {
    // 恢复现场
    let polygons = util.session('editing-polygons');
    if(polygons){
      this.polygons = polygons;
    }
    let roads = util.session('editing-roads');
    if(roads){
      this.roads = roads;
    }
  },
  mounted: function(){
    this.mainDom = document.getElementById('svgElements');
    this.mapDataDownloadDom = document.getElementById('mapDataDownload');
    this.pathDataDownloadDom = document.getElementById('pathDataDownload');
  }
};
</script>

<style>
@import "assets/common.css";
/* 图层 */
.area-wrap {
  position: relative;
  width: 1000px;
  height: 1000px;
  background: #fff;
  border:1px solid #eee;
}
.layer {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.svgPath {
  background:url(assets/img/boxline.png);
}
/* svg地图 */
.svgArea {
  stroke-width: 0.4;
  stroke: #ec6707;
  fill: #f8d1b5;
}
.svgArea.active {
  stroke: #000 !important;
  stroke-width:1; 
  stroke-dasharray: 10;
}

.svgArea.isBlock{
  fill: #ccc !important;
  stroke: #fff !important
}
/* 漂浮 */
.mapFloat {
  background: none;
  pointer-events: none;
  color: #333;
}
.mapFloat:empty {
  z-index: -1;
}
.mapFloat.handle {
  pointer-events: auto;
}
.mapFloat ._title {
  position: absolute;
  transform: translate3d(-2em, -14px, 0);
  pointer-events: none;
  font-size: 10px;
}
.mapFloat ._chart {
  position: absolute;
  width: 50px;
  height: 50px;
  transform: translate3d(-32px, -32px, 0);
}
.mapFloat ._pin {
  width: 64px;
  height: 64px;
  background: url(/public/img/pin.png) center no-repeat;
  text-align: center;
  line-height: 64px;
  color: #fff;
  font-size: 12px;
  font-weight: 700;
}
/* 绘制路径 */
.drawPoint{position: absolute;width:6px;height: 6px;border-radius: 6px;overflow:hidden;background:violet;transform: translate3d(-3px, -3px, 0);cursor: pointer;}
.drawPoint:hover, .drawPoint.active{box-shadow: 0 0 4px 4px brown;}
polyline.editingPath{fill:none;stroke:#ec6707;stroke-width:2}
polyline.editingPath.active{stroke: #49a25a;stroke-width:4}
/* 面板 */
.pathList ._pathtag{margin:0 .5em .5em 0;cursor: pointer;}
.myUpload .el-upload,.myUpload .el-upload-dragger{width:100%;}
/* 建筑类型参照 */
.typeMarks{text-align: center;font-size:10px;padding:.5em;}
.typeMarks ._mark{position: relative; display: inline-block;margin:2em 0 0; width:5em;}
.typeMarks ._mark::before{content: "";position: absolute;width:0;height: 0; left: 50%;margin-left: -1em;bottom:100%;border:1em solid;}
</style>
